.file-bar {
  margin-top: 37px;
  height: 34px;
  width: 100%;
  display: flex;
  justify-content: left;
  margin-left: -4px;
  overflow-x: hidden;
}

.vscode-btns {
  height: 30px;
  width: 115px;
  background-color: hsl(230, 41%, 70%);
  border-radius: 5px 5px 0px 0px;
  font-size: 15px;
  font-weight: 400;
  font-family: monospace;
}
.coding {
  height: 100%;
  width: 100%;
  background-color: hsl(230, 41%, 70%);
  outline: none;
  border: none;
  padding: 10px;
  padding-right: 100px;
  font-family: monospace;
  font-size: 15px;
  font-weight: 700;
}

.code {
  flex: 1;
  width: 100%;
  background-color: hsl(230, 41%, 70%);
  margin-top: -4px;
  border-radius: 0px;
  border-top: 1px solid gray;
}
